.section-calculator {

    padding: 50px 0;
    overflow: visible;

    @media screen and (min-width: $w992) {
        padding: 40px 0;
    }

    & .container {

        @media screen and (min-width: $w1440) {
            max-width: 1340px;
        }

        @media screen and (min-width: $w1376) {
            max-width: 1200px;
        }

    }
}

.calculator {

    &[v-cloak] {
        opacity: 0;
    }

    @media screen and (min-width: $w992) {
        display: flex;
    }

    &__title {
        font-weight: 700;
        font-size: 32px;
        line-height: 65px;
        color: #515151;
        line-height: 120%;
        padding-bottom: 30px;

        @media screen and (min-width: $w1376) {
            font-size: 48px;
        }

        @media screen and (min-width: $w992) {
            font-size: 36px;
        }
    }

    &__main {






        @media screen and (min-width: $w1440) {
            padding-right: 130px;
        }

        @media screen and (min-width: $w1200) {
            padding-right: 60px;
        }

        @media screen and (min-width: $w992) {
            padding-right: 30px;
            width: 68%;
        }

    }

    &__aside {

        @media screen and (min-width: $w992) {
            width: 32%;
        }

        & form {
            position: sticky;
            top: 30px;
        }
    }


}

.param-calculator {

    margin-bottom: 40px;

    &--color {

        & .param-calculator {
            &__props {

                & label {
                    @media screen and (max-width: $w576) {
                        width: 100%;
                    }
                }
            }
        }

    }

    &__desc {
        font-size: 13px;
        line-height: 140%;
        background-color: #eee;
        border-left: 3px solid $blue;
        padding: 10px 25px;
        margin-top: 30px;

        @media screen and (min-width: $w480) {
            font-size: 14px;
            line-height: 140%;
        }

        & p {
            margin: 7px 0;
            animation: slide-up 0.3s ease;
        }
    }

    &__title {
        font-size: 18px;
        font-weight: bold;
        padding-bottom: 20px;
        color: #515151;
    }

    &__props {

        display: flex;
        flex-wrap: wrap;
        margin: -5px;

        @media screen and (min-width: $w1376) {
            margin: -15px;
        }

        & label {
            width: 100%;
            padding: 5px;
            display: block;
            cursor: pointer;

            @media screen and (min-width: $w359) {
                width: 50%;
            }

            @media screen and (min-width: $w1376) {
                padding: 15px;
            }

            & input {
                display: none;
            }

            & input:checked~.checkbox-image {
                border: 2px solid $blue;
                background-color: #eee;

                & img {

                    transform: scale(1.2);
                }
            }

            & input:checked~.radio-prop {

                border: 2px solid $blue;
                background-color: #eee;

                & .radio-prop__elem::before {
                    transform: scale(1);
                    transition: 0.3s ease;
                }
            }

        }
    }

    &__input {
        padding: 5px;
        width: 100%;

        @media screen and (min-width: $w1376) {
            padding: 15px;
        }

        & input {
            height: 55px;
            border: 1px solid #CBCBCB;
            padding-left: 20px;
            font-size: 14px;
            width: 100%;
            border-radius: 4px;
            transition: 0.3s ease;

            @media screen and (min-width: $w576) {
                height: 65px;
                padding-left: 25px;
                font-size: 16px;
            }

            &:focus {
                border: 2px solid $blue;
            }

        }
    }
}

.checkbox-image {
    background-color: #F5F5F5;
    padding: 10px 10px 10px 10px;
    border: 1px solid #9E9E9E;
    height: 100%;
    border-radius: 5px;

    &:hover {
        border: 1px solid $blue;
    }

    @media screen and (min-width: $w760) {
        padding: 25px 27px 33px 27px;
    }

    @media screen and (min-width: $w480) {
        padding: 10px 20px 10px 20px;
    }

    &__title {
        font-weight: 600;
        font-size: 13px;
        line-height: 140%;
        text-align: center;
        margin-bottom: 10px;

        @media screen and (min-width: $w760) {
            font-size: 16px;
        }
    }

    &__desc {
        font-size: 13px;
        line-height: 150%;

        @media screen and (min-width: $w1376) {
            font-size: 14px;
        }

    }

    &__image {

        margin-bottom: 25px;

        & picture {
            display: block;

            width: calc(50vw - 80px);
            height: calc(50vw - 80px);
            position: relative;
            margin: 0 auto;

            @media screen and (min-width: $w480) {
                max-width: 225px;
                height: 225px;
            }

            & img {
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
                mix-blend-mode: multiply;
                max-width: 95%;
                max-height: 95%;
                object-fit: contain;
                margin: auto;
                transition: 0.3s ease;
            }

        }
    }
}

.radio-prop {
    border: 1px solid #9E9E9E;
    padding: 5px;
    min-height: 55px;
    background-color: #F5F5F5;
    border-radius: 4px;
    position: relative;
    display: flex;

    &:hover {
        border: 1px solid $blue;
    }

    @media screen and (min-width: $w760) {
        min-height: 65px;
    }

    &__elem {

        width: 18px;
        height: 18px;
        border: 1px solid $blue;
        border-radius: 50%;
        position: absolute;
        right: 15px;
        top: 50%;
        transform: translateY(-50%);

        &::before {
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background-color: $blue;
            content: '';
            display: block;
            left: 4px;
            top: 4px;
            position: absolute;
            transform: scale(0);
        }

    }

    &__text {
        flex-grow: 1;
        padding-right: 30px;
        display: flex;
        align-items: center;
        padding-left: 15px;
        font-size: 14px;
        font-weight: bold;
        color: #515151;

        @media screen and (min-width: $w760) {
            font-size: 16px;
        }
    }

    &__color {
        width: 55px;
        margin: -5px;
        border-right: 1px solid #9E9E9E;
        transition: 0.3s ease;

        @media screen and (min-width: $w576) {
            width: 65px;
        }


    }
}

.calculator-result {

    background-color: #ECECEC;
    border-radius: 28px;
    padding: 30px;

    @media screen and (min-width: $w760) {
        padding: 15px 30px 30px 30px;
    }

    @media screen and (min-width: $w1440) {
        padding: 60px;
    }

    &__err {
        font-size: 14px;
        color: #000;
        margin-bottom: 20px;
        background-color: #e0e0e0;
        padding: 7px 10px;
        border-left: 2px solid #f00;
    }


    &__title {
        font-weight: 600;
        font-size: 25px;
        line-height: 41px;
        color: #515151;
        padding-bottom: 10px;

        @media screen and (min-width: $w1376) {
            padding-bottom: 25px;
            font-size: 36px;
        }
    }

    &__desc {
        font-size: 13px;
        padding-bottom: 15px;
        font-weight: 600;
        color: #515151;

        @media screen and (min-width: $w1376) {
            font-size: 14px;
            padding-bottom: 35px;
        }

        & ul {
            & li {
                display: flex;
                justify-content: space-between;
                border-bottom: 1px dashed $blue;
                padding: 5px 0 7px 0;

                & span {
                    &:first-child {}

                    &:last-child {
                        font-weight: normal;
                    }
                }

                &:last-child {
                    border-bottom: 0;
                }
            }
        }
    }

    &__cost {
        padding-bottom: 25px;
        font-size: 28px;
        font-weight: 600;
        color: #515151;

        @media screen and (min-width: $w1376) {
            font-size: 36px;
            padding-bottom: 45px;
        }
    }

    &__submit {
        & button {
            background: #B161ED;
            border-radius: 0px 28px;
            border: none;
            width: 100%;
            height: 65px;
            font-weight: 700;
            font-size: 24px;
            text-transform: uppercase;
            color: #FFFFFF;
            cursor: pointer;
            transition: 0.3s ease;

            @media screen and (max-width: $w1376) {
                font-size: 18px;
            }

            &:hover {
                opacity: 0.9;
            }
        }

        margin-left: -30px;
        margin-right: -30px;
        margin-bottom: -30px;

        @media screen and (min-width: $w760) {
            margin-left: -30px;
            margin-right: -30px;
            margin-bottom: -30px;
        }

        @media screen and (min-width: $w1440) {
            margin-left: -60px;
            margin-right: -60px;
            margin-bottom: -60px;
        }
    }

    &__form {
        padding-bottom: 30px;

        & .form {
            & input {
                height: 50px;
                font-size: 14px;
                padding-left: 20px;
                border-radius: 5px;
                transition: 0.3s ease;

                &.err {
                    border-color: #f00;
                }

                &:focus {
                    border-color: $blue;

                }

                @media screen and (min-width: $w1376) {
                    height: 55px;
                    font-size: 16px;
                }
            }
        }
    }

    &__status {
        & span {

            text-align: center;

            &.err {
                color: #f00;
                font-size: 13px;
                margin-bottom: 30px;
                display: block;
            }

            &.msg {
                color: rgb(7, 206, 0);
                font-size: 13px;
                margin-bottom: 30px;
                display: block;
            }
        }
    }


}


.modal-callback.open.fadeout {
    opacity: 0 !important;
    transition: 0.6s ease 0.3s;
    animation: none;
}

.list-enter-active {
    transition: all 0.4s ease;
    opacity: 1;
}

.list-leave-active {}

.list-enter {
    opacity: 0;
    transform: translateX(-10px);
}

.list-enter-to {
    opacity: 1;
    transform: translateX(0);
}

.list-leave {
    display: none;
}

.list-leave-to {
    display: none;
}