footer {
    background-color: #515151;
    position: relative;
    padding: 40px 0;

    @media screen and (min-width: $w760) {
        padding-top: 60px;
        padding-bottom: 60px;
    }

    &::before {
        position: absolute;
        left: 0;
        right: 0;
        top: 5px;
        height: 5px;
        left: 0;
        width: 100%;
        background-color: #fff;
        content: '';
    }
}

.footer {

    color: #fff;

    &__content {
        display: flex;
        flex-direction: column-reverse;

        @media screen and (min-width: $w760) {
            flex-direction: row;
            justify-content: space-between;
            padding-bottom: 30px;
        }
    }


    & a {
        color: #fff;

        &:hover {
            color: $blue;
        }
    }

    &__social {
        display: flex;
        flex-direction: column-reverse;

        @media screen and (min-width: $w760) {
            flex-direction: column;
        }

        & span {
            display: block;
            text-align: center;
            font-size: 13px;
            text-transform: uppercase;

            @media screen and (min-width: $w760) {
                font-size: 16px;
                max-width: 180px;
                margin: 0 auto;
                padding-top: 15px;
            }
        }

        & ul {

            display: flex;
            justify-content: center;
            padding-top: 15px;

            & li {
                padding: 0 5px;

                &:hover {
                    & svg {
                        fill: $blue;
                    }
                }
            }

            & svg {
                width: 45px;
                height: 45px;
                fill: #fff;
                transition: 0.3s ease;
            }
        }
    }

    &__menu {

        @media screen and (min-width: $w760) {
            padding-right: 30px;
        }

        & li {
            text-align: center;

            @media screen and (min-width: $w760) {
                text-align: left;
            }

            & a {
                font-size: 12px;
                text-transform: uppercase;
                color: #fff;
                transition: all 0.3s;

                &:hover {
                    color: $blue;
                }


            }
        }
    }

    &__contact {
        padding-top: 30px;
        margin: 0 auto;
        display: table;

        @media screen and (min-width: $w760) {
            padding-top: 0;
        }

        &>div {
            padding-bottom: 20px;
        }
    }

    &__phone,
    &__worktime,
    &__address {


        display: flex;
        align-items: center;
        font-size: 13px;
        margin: 0 auto;

        & a {
            &:hover {
                text-decoration: underline;
            }
        }

        & svg {
            margin-right: 10px;
        }

        & address {
            font-style: normal;
        }

    }

    &__sl {

        border-bottom: 2px solid $blue;
        margin-left: -25px;
        margin-right: -25px;
        margin-bottom: 30px;

        @media screen and (min-width: $w760) {
            display: none;
        }

        & .bgimage {
            width: 280px;
            height: 56px;
            @include bgimage-contain;
            margin: 0 auto;
        }
    }

    &__right {
        @media screen and (min-width: $w760) {
            display: flex;
        }
    }


}

.footer-sl {

    display: none;

    @media screen and (min-width: $w760) {
        display: block;

    }

    border-bottom: 2px solid $blue;

    & .bgimage {
        width: 280px;
        height: 56px;
        @include bgimage-contain;
        margin: 0 auto;

        @media screen and (min-width: $w1200) {
            width: 580px;
            height: 75px;
            background-color: #515151;
            padding-left: 40px;
            padding-right: 40px;
            margin-bottom: -20px;
            background-size: 375px 75px;
        }
    }
}