header {
    background-color: #515151;
    color: #fff;

    & a {
        color: #fff;
    }

    & .container {
        max-width: 1410px;
    }
}

.header {
    display: flex;
    align-items: center;
    padding: 10px 0;
    position: relative;

    @media screen and (min-width: $w760) {
        padding: 0 0;
    }

    &__logo {

        position: relative;
        display: flex;
        align-self: flex-start;
        margin-top: -10px;

        @media screen and (min-width: $w760) {
            align-self: center;
            margin-top: 0;
        }


        &-image {

            min-width: 110px;
            width: 110px;
            height: 44px;
            z-index: 1;
            overflow: hidden;
            cursor: pointer;
            background-color: #1DB9C3;
            border-radius: 0 0 10px 10px;

            -webkit-box-shadow: 9px 9px 11px 0px rgba(59, 58, 58, 0.8);
            box-shadow: 9px 9px 11px 0px rgba(59, 58, 58, 0.8);
            display: -ms-flexbox;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            z-index: 1;

            @media screen and (min-width: $w760) {
                min-width: 208px;
                width: 208px;
                height: 84px;
                margin-bottom: -26px;

            }

            & .bgimage {
                width: 95px;
                height: 30px;
                @include bgimage-contain;

                @media screen and (min-width: $w760) {
                    width: 179px;
                    height: 156px;
                }
            }
        }

        &-text {

            position: absolute;
            left: 0;
            width: 180px;
            bottom: auto;
            top: calc(100% + 10px);
            font-size: 18px;
            background-color: #fff;
            border-radius: 0 0 10px 0;
            color: #000;
            font-family: 'Caveat', cursive;
            -webkit-box-shadow: 6px 5px 7px -7px rgba(59, 58, 58, 0.6);
            box-shadow: 6px 5px 7px -7px rgba(59, 58, 58, 0.6);
            padding: 5px;
            line-height: 100%;


            @media screen and (min-width: $w760) {
                position: static;
                width: auto;
                background-color: #fff;
                border-radius: 0 0 10px 0;
                padding: 0px 12px 4px 12px;
                text-transform: lowercase;
                font-family: 'Caveat', cursive;
                font-size: 22px;
                color: #515151;
                -webkit-box-shadow: 9px 9px 11px 0px rgba(59, 58, 58, 0.8);
                box-shadow: 9px 9px 11px 0px rgba(59, 58, 58, 0.8);
                max-width: 220px;
                display: flex;
                align-items: center;
            }
        }
    }



    &__phone,
    &__worktime,
    &__address {


        display: flex;
        align-items: center;
        font-size: 13px;
        margin: 0 auto;

        & a {
            &:hover {
                text-decoration: underline;
            }
        }

        & svg {
            margin-right: 10px;
        }

        & address {
            font-style: normal;
        }

    }


    &__worktime,
    &__address {
        display: none;

        @media screen and (min-width: $w760) {
            display: flex;
        }
    }

    &__phone {

        margin-left: auto;
        margin-right: 0;

        @media screen and (min-width: $w760) {
            margin-left: auto;
            margin-right: auto;
        }
    }

    &__callback {

        display: none;


        @media screen and (min-width: $w992) {
            display: block;
        }

        & a {
            display: flex;
            background-color: #C475FF;
            border-radius: 10px 0;
            padding: 6px 15px;

            &:hover {
                opacity: 0.95;
            }

            & svg {
                margin-right: 10px;
                position: relative;
                top: 3px;
            }

            & span {
                display: flex;
                flex-direction: column;
                line-height: 100%;
                text-transform: uppercase;
                font-weight: 600;
                font-size: 19px;

                & small {
                    max-width: 85px;
                    font-size: 16px;
                }
            }
        }
    }

    &__burger {

        background-color: $blue;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 40px;
        border-radius: 0 0px 5px 5px;
        position: absolute;
        right: 0;
        top: 100%;
        cursor: pointer;
        z-index: 2;

        @media screen and (min-width: $w760) {
            display: none;
        }

        & span {
            width: 25px;
            height: 2px;
            display: inline-block;
            background-color: #eee;
            margin-bottom: 6px;


            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}