.modal-callback {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 100;
    background: rgba(0, 0, 0, 0.5);
    display: none;
    justify-content: center;
    align-items: center;
    opacity: 0;
    animation: fadein 0.3s ease forwards;


    &.open {

        display: flex;
        overflow-y: auto;

        & .modal-callback {
            &__wrp {
                margin: auto;
            }
        }

    }

    &__close {
        position: absolute;
        color: #fff;
        transform: rotate(45deg);
        right: 16px;
        top: 7px;
        font-size: 40px;
        line-height: 40px;
        cursor: pointer;
        font-weight: 300;
        display: block;
    }

    &__wrp {
        width: 100%;
        max-width: 560px;
        border-radius: 28px 28px 28px 0;
        overflow: hidden;
        position: relative;
    }

    &__content {
        background-color: #f5f5f5;

    }

    &__submit {
        & button {
            background: #B161ED;
            border-radius: 0;
            border: none;
            width: 100%;
            height: 65px;
            font-weight: 700;
            font-size: 24px;
            text-transform: uppercase;
            color: #FFFFFF;
            cursor: pointer;
            transition: 0.3s ease;

            @media screen and (max-width: $w1376) {
                font-size: 18px;
            }

            &:hover {
                opacity: 0.9;
            }
        }




    }

    &__title {
        height: 60px;
        background-color: #1DB9C3;
        line-height: 60px;
        text-align: center;
        color: #fff;
        font-size: 21px;
        text-transform: uppercase;
        font-weight: 700;
        margin-bottom: 30px;
    }

    &__subtitle {
        max-width: 400px;
        margin: 0 auto;
        font-size: 22px;
        color: #515151;
        line-height: 32px;
        text-align: center;
    }

    &__form {
        padding-bottom: 30px;
        padding: 30px;

        @media screen and (min-width: $w760) {
            padding: 50px 30px;
        }

        & .form {

            max-width: 390px;
            margin: 0 auto;

            & input {
                height: 50px;
                font-size: 14px;
                padding-left: 20px;
                border-radius: 5px;
                transition: 0.3s ease;

                &.err {
                    border-color: #f00;
                }

                &:focus {
                    border-color: $blue;

                }

                @media screen and (min-width: $w1376) {
                    height: 50px;
                    font-size: 16px;
                }
            }
        }
    }

    &__status {
        & span {

            text-align: center;

            &.err {
                color: #f00;
                font-size: 13px;
                margin-bottom: 30px;
                display: block;
            }

            &.msg {
                color: rgb(7, 206, 0);
                font-size: 13px;
                margin-bottom: 30px;
                display: block;
            }
        }
    }
}

.nav-main {

    @keyframes fadein {
        from {
            opacity: 0;
        }

        to {
            opacity: 1;
        }
    }

    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 100;
    background: rgba(0, 0, 0, 0.5);
    display: none;
    justify-content: center;
    align-items: center;
    opacity: 0;
    animation: fadein 0.3s ease forwards;


    &.open {
        display: flex;
        overflow-y: auto;

        & .container {
            margin: auto;
        }
    }

    @media screen and (min-width: $w760) {
        display: block;
        background-color: #515151;
        height: auto;
        padding: 8px 0;
        position: static;
    }

    & ul {
        display: flex;
        align-items: center;

        & li {
            flex-grow: 1;

            & a {
                display: block;
                width: 100%;
                text-align: center;
                color: #fff;
                text-transform: uppercase;
                font-size: 17px;
                transition: all 0.3s;
                cursor: pointer;
                line-height: 17px;


                &:hover {
                    color: $blue;
                }
            }
        }
    }

    & nav {
        @media screen and (max-width: $w760) {
            background-color: rgba(29, 185, 195, 0.8);
            border-radius: 20px;
            z-index: 100;
            position: relative;



            & ul {
                display: flex;
                flex-direction: column;
                -webkit-box-orient: vertical;
                -webkit-box-direction: normal;
                -ms-flex-direction: column;
                flex-direction: column;
                -webkit-box-align: center;
                -ms-flex-align: center;
                align-items: center;
                margin-bottom: 10px;
                margin-top: 30px;
                padding: 30px 0;

                & li {
                    margin: 10px 10px 0 10px;
                    width: 67%;
                    text-align: center;
                    border-bottom: 1px solid #85E9EF;
                    padding-bottom: 10px;
                    text-align: center;

                    &:hover {
                        & a {
                            color: #fff;
                            transform: translateX(10px);
                        }
                    }

                    &:last-child {
                        border: none;
                    }

                    & a {
                        line-height: 33px;
                        font-size: 17px;
                        font-weight: 400;
                        text-align: center;
                        margin: 0 auto;
                        transition: transform 0.3s ease;


                    }
                }
            }


        }
    }

    & .close-mobile-menu {

        color: #fff;
        transform: rotate(45deg);
        position: absolute;
        right: 15px;
        top: 12px;
        font-size: 28px;
        cursor: pointer;


        @media screen and (min-width: $w760) {
            display: none;
        }
    }
}