.main-slider {

    position: relative;
    padding-top: 50px;

    @media screen and (min-width: $w760) {
        padding-top: 0;
    }

    &__slide {

        background-color: #eee;

        & picture {
            display: block;
            padding-top: 50%;
            position: relative;

            @media screen and (min-width: $w992) {
                padding-top: 15%;
            }

            & img {
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
                object-fit: cover;
                width: 100%;
                height: 100%;
            }
        }
    }

    &__nav {

        position: absolute;
        z-index: 10;
        top: 50%;
        right: 5%;
        left: 5%;
        display: flex;
        justify-content: space-between;

        & button {
            background-color: rgba(255, 255, 255, 0.5) !important;
            border-radius: 15px;
            width: 35px;
            height: 40px;
            margin-top: -20px;
            border: none;
            position: relative;

            @media screen and (min-width: $w760) {
                width: 51px;
                height: 62px;
                margin-top: -31px;
            }

            & span {
                font-size: 35px;
                line-height: 14px;
                height: 25px;
                position: absolute;
                margin: auto;
                left: 0;
                top: 0;
                right: 0;
                bottom: 0;

                @media screen and (min-width: $w760) {
                    font-size: 63px;
                    line-height: 10px;
                }
            }
        }
    }
}